import React from 'react';
import './Login.scss';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

import { Field, reduxForm } from 'redux-form';

class LoginContainer extends React.Component{
  constructor(props){
    super(props);
    const { handleSubmit } = props;
  }

  handleSubmit = () => {
    this.props.router.push('/user-buyDroplet');
    //call cognito here
  }

  loginforgot = () => {
    this.props.router.push('/forgot-email');
  }

  goToIndex = () => {
      this.props.router.push('/');
  }

  checkResetMsg = () => {
    if(this.props.match && this.props.match.params.isMsg){
    return (<div className="form-helper-text reset-msg">
      Please check your email and follow the instructions within to login with your new password.
    </div>);
  }

  }

  renderField = ({input,label,type,placeholder, meta: { touched, error, warning }}) => (
    <div className="form-group">
      <label htmlFor="rePass">{label}</label>
      <input {...input} className="form-control" placeholder={placeholder} type={type} />
      {touched && error && <span className="wrong-email-or-passw">{error}</span>}

    </div>
)

  render() {
    let title;
    console.log('login render ', this.props.match);
    if(this.props.match){
      console.log('props params: ',this.props.match.params);
      title = this.props.match.params.isMsg? "Your New Password Has Been Reset" : "Login to Your Droplet Account";
    }
      return (
          <div className="login-form">
              <div className="header-section">
                  <div className="header-items">
                      <div className="header-left">
                          <img src={logoImg} className="droplet-logo" onClick={this.goToIndex}/>
                      </div>
                      <div className="header-right">
                          <label className="header-text pointer" onClick={this.goToLogin}>Login</label>
                      </div>
                  </div>
              </div>

              <div className="container history-container">
                  <div className="row">
                      <div className="offset-1 col-10">
                          {this.checkResetMsg}
                          <div className="step-section">
                              <h4 className="step-text">{title}</h4>
                          </div>
                          <div className="password-section">
                            <form onSubmit={this.handleSubmit} noValidate>
                              <div className="form-group">
                                <Field className="form-control" placeholder="elsonng@gmail.com" label="Your Email" name="email" component={this.renderField} type="email" />
                              </div>
                              <div className="form-group">
                                <Field className="form-control" placeholder="**********" label="Password" name="password" component={this.renderField} type="password" />
                              </div>
                              <button className="btn btn-green btn-checkout">LOGIN</button>
                            </form>
                              <div className="form-helper-text">Forgot your password? <a onClick={this.loginforgot}>Click here to reset</a></div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      );
  }



}

const validate = values => {
  const errors = {}
  if (!values.email) {
    errors.email = 'Required'
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = 'Invalid email address'
  }
  if (!values.password) {
    errors.password = 'Required'
  }
  return errors
}

export default reduxForm({form: 'contact',validate})(LoginContainer)



